    /* 네모 메모 css */
    .MemoDetail-backdrop {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, 0.4);
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 1000;

    }

    .MemoDetail-content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        background-color: #fff3bf;
        padding: 20px;
        border-radius: 8px;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
        width: 300px;
        height: 300px;
        border: 1px solid #ffd84d;
        overflow: auto;
        position: relative;
    }

    .MemoDetail-content p {
        margin: 0 0 20px 0;
    }

    .MemoDetail-content button {
        margin: 10px;
        padding: 8px 16px;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        background-color: #f0c14b;
        color: #333;
    }

    .MemoDetail-content button:hover {
        background-color: #e7b035;
    }


    .MemoDetail-buttons {
        position: absolute;
        right: 20px;
        bottom: 20px;
        display: flex;
    }

    .MemoDetail-content button {
        margin-left: 10px;
    }

    .MemoDetail-content button:first-child {
        margin-left: 0;
    }